.header {
  width: $mobile-width;
  margin: 0 auto;
  text-align: left;
}

.header__image {
  width: 191px;
  height: 39px;
  padding: 11px 0 12px 20px;
  text-align: left;
}

.header__logo:hover .header__image {
  opacity: 0.8;
}

.header__logo:focus .header__image {
  opacity: 0.6;
}

@media (min-width: $tablet-width) {
  .header {
    display: flex;
    justify-content: space-between;
    width: $tablet-width;
    padding: 12px $tablet-padding 55px;
    box-sizing: border-box;
  }

  .header__image {
    padding: 0;
    width: 174px;
    height: 59px;
  }
}

@media (min-width: $desktop-width) {
  .header {
    width: $desktop-width;
    margin: 0 auto 34px;
    padding: 53px $desktop-padding 71px;
    box-sizing: border-box;

    &--margin {
      margin-bottom: 0;
    }
  }

  .header__image {
    width: 203px;
    height: 60px;
  }
}
