.advantages {
  width: $mobile-width;
  margin: 0 auto;
  padding: 0 $mobile-padding 45px;
  box-sizing: border-box;
  text-align: left;
}

.advantages__title {
  margin: 0;
  font-size: 36px;
  line-height: 60px;
  font-weight: 400;
}

.advantages__text {
  position: relative;
  margin: 0;
  padding: 27px 6px 0 80px;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: $light-black;

  &::after {
    content: "";
    position: absolute;
    top: 26px;
    left: 0;
    width: 60px;
    height: 60px;
    background-color: $green;
  }

  &--leaf::before {
    content: "";
    position: absolute;
    top: 33px;
    left: 5px;
    width: 40px;
    height: 40px;
    background: url("../img/advantage-leaf@1x.png") no-repeat 0 0;
    z-index: 1;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/advantage-leaf@2x.png");
      background-size: 50px 50px;
    }
  }

  &--powder::before {
    content: "";
    position: absolute;
    top: 30px;
    left: 5px;
    width: 40px;
    height: 40px;
    background: url("../img/advantage-powder@1x.png") no-repeat 0 0;
    z-index: 1;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/advantage-powder@2x.png");
      background-size: 50px 50px;
    }
  }

  &--dining::before {
    content: "";
    position: absolute;
    top: 30px;
    left: 4px;
    width: 40px;
    height: 40px;
    background: url("../img/advantage-dining@1x.png") no-repeat 0 0;
    z-index: 1;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/advantage-dining@2x.png");
      background-size: 50px 50px;
    }
  }

  &--clock::before {
    content: "";
    position: absolute;
    top: 29px;
    left: 5px;
    width: 40px;
    height: 40px;
    background: url("../img/advantage-clock@1x.png") no-repeat 0 0;
    z-index: 1;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/advantage-clock@2x.png");
      background-size: 50px 50px;
    }
  }
}

.advantages__number {
  display: none;
}

@media (min-width: $tablet-width) {
  .advantages {
    display: flex;
    flex-wrap: wrap;
    width: $tablet-width;
    margin: 0 auto;
    padding: 0 $tablet-padding;
    box-sizing: border-box;
    text-align: left;
  }

  .advantages__title {
    font-size: 60px;
    padding-bottom: 45px;
  }

  .advantages__wrapper {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }

  .advantages__text {
    position: relative;
    width: 245px;
    margin: 0;
    padding: 140px 0 105px;
    font-size: 16px;
    line-height: 24px;

    &:nth-child(odd) {
      margin-right: 140px;
    }

    &::after {
      top: 27px;
      width: 80px;
      height: 80px;
    }

    &--leaf::before {
      top: 42px;
      left: 14px;
      width: 40px;
      height: 40px;
    }

    &--powder::before {
      top: 42px;
      left: 13px;
      width: 40px;
      height: 40px;
    }

    &--dining::before {
      top: 41px;
      left: 14px;
      width: 40px;
      height: 40px;
    }

    &--clock::before {
      top: 41px;
      left: 13px;
      width: 40px;
      height: 40px;
    }
  }

  .advantages__number {
    display: block;
    position: absolute;
    top: -10px;
    right: 0;
    font-family: "Oswald", "Arial", sans-serif;
    font-size: 281px;
    line-height: 281px;
    color: $grey;
    z-index: -1;
  }
}

@media (min-width: $desktop-width) {
  .advantages {
    width: $desktop-width;
    padding: 0 $desktop-padding 5px;
    box-sizing: border-box;
  }

  .advantages__text:nth-child(odd),
  .advantages__text {
    margin-right: 80px;
  }

  .advantages__text:last-child {
    margin-right: 0;
  }
}
